.fade-enter{
    opacity: 0;
 }
 .fade-exit{
    opacity: 1;
 }
 .fade-enter-active{
    opacity: 1;
 }
 .fade-exit-active{
    opacity: 0;
 }
 .fade-enter-active,
 .fade-exit-active{
    transition: opacity 300ms;
 }

 .crossfadableProductDescription {
    transition: opacity 0.3s;
    overflow: hidden;
    height: 100% !important;
  }
  
  .before-enter {
    opacity: 0;
  }
  
  .entering {
    opacity: 1;
    transition: opacity 0.3s;
  }
  
  .before-leave {
    opacity: 1;
  }
  
  .leaving {
    opacity: 0;
    transition: opacity 0.3s;
  }


.featured-clickable-links {
    color: rgb(72, 63, 127);
    font-weight: 600;
    letter-spacing: -0.75px;
    font-size: 18pt;
}

.featured-clickable-links:hover {
    text-decoration: underline;
}

.featured-clickable-links span svg {
    font-size: 13pt;
    margin-bottom: 1px;
}

